html, body {
  width: 100%;
  height: 100%;
  margin: 0 auto;
  font-family: Sans-Serif;
}

input, select {
  width: calc(100% - 20px);
  height: 100%;
  padding-left: 10px;
  padding-right: 10px;
  color: #000000;
  background-color: transparent;
  border: 0;
  outline: 0;
}

input::placeholder {
  color: #888888;
}

.hide-scrollbar::-webkit-scrollbar {
  display: none;
}

.hide-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}

.loader {
  width: 50px;
  aspect-ratio: 1;
  border-radius: 50%;
  background:
    radial-gradient(farthest-side,#2e86de 94%,#0000) top/8px 8px no-repeat,
    conic-gradient(#0000 30%,#2e86de);
  -webkit-mask: radial-gradient(farthest-side,#0000 calc(100% - 8px),#000 0);
  animation: l13 1s infinite linear;
}

@keyframes l13{
  100%{transform: rotate(1turn)}
}
